@use '../component/variables' as *;

.header {
    padding: 60px 0 80px;
    position: relative;
    background-image: url('../../img/header_bg.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    h1 {
        font: 400 60px $second-family;
        color: #222;
        span {
            font: 700 60px $third-family;
            color: #ff9549
        }
    }
    &__info {
       max-width: 600px; 
    }
    &__text {
        margin: 20px 0 0;
        font: 400 28px $second-family;
        color: #222;
        opacity: .9;
        span {
            font: 700 28px $third-family;
        }
    }
    &__dir {
        margin: 60px 0 0;
        display: grid;
        grid-template-columns: 308px 1fr;
        gap: 20px;
        align-items: center;
        &_sign {
            width: 206px;
        }
        p {
            font: 400 18px $font-family;
            color: #222;
            opacity: .6;
            margin: 14px 0 0; 
            &+p {
                font: 400 28px $second-family;
                color: #222;
                opacity: 1;
                margin: 3px 0 0; 
            }
        }
    }
}

@media (max-width: 768px) {
    .header {
        padding: 30px 0;
        background: url('../../img/header_bg_mob.jpg') center no-repeat;
        background-size: cover;
        h1 {
            font-size: 34px;
            span {
                font-size: 34px;
            }
        }
        &__text {
            margin: 20px 0 0;
            font-size: 24px;
        }
        &__dir {
            grid-template-columns: 1fr;
            max-width: 390px;
            padding: 0 0 0 240px;
            position: relative;
            &_pers {
                position: absolute;
                left: 0;
                // top: 50%;
                // transform: translateY(-50%);
                bottom: 0;
                width: 100%;
                max-width: 310px;
            }
            &_sign {
                width: 150px;
            }
            p {
                font-size: 14px; 
                &+p {
                    margin: 2px 0 0;
                    font: 400 22px $second-family; 
                }
            }
        }
    }
}

@media (max-width: 420px) {
    .header {
        &__dir {
            grid-template-columns: 1fr;
            &_pers {
                order: 2;
            }
        }
    }
}